<template>
    <q-chip
        :style="{
            maxWidth: '175px',
            border: '1px solid ' + c.color,
            backgroundColor: modelValue[c.id] ? c.color : '',
        }"
        clickable
        v-for="c in categories"
        :dense="categories.length > 8"
        :key="c.id"
        :selected="modelValue[c.id]"
        @update:selected="updateValue(c.id, $event)"
    >
        <div class="ellipsis">
            {{ c.name }}
            <q-tooltip> {{ c.name }}</q-tooltip>
        </div>
    </q-chip>
</template>

<script lang="ts">
import { useQuasar } from "quasar";
import { computed, defineComponent, PropType } from "vue";

import { Category } from "@/store/types";

export default defineComponent({
    name: "CategoryChips",
    emits: ["update:modelValue"],
    props: {
        modelValue: {
            type: Object as PropType<{ [key: string]: boolean }>,
        },
        categories: {
            type: Array as PropType<Array<Category>>,
            default: [],
        },
    },
    setup: (props, ctx) => {
        // Providers
        const $q = useQuasar();

        // Computed
        const darkActive = computed(() => $q.dark.isActive);

        // Functions
        const updateValue = (id: string, val: boolean) => {
            if (!props.modelValue) return;
            ctx.emit("update:modelValue", {
                ...Object.entries(props.modelValue)
                    .filter((v) => !!v[1])
                    .reduce((acc, kv) => (acc = { ...acc, [kv[0]]: kv[1] }), {}),
                [id]: val,
            });
        };

        return { updateValue, darkActive };
    },
});
</script>
