
import useDialogPluginComponent from 'quasar/src/composables/use-dialog-plugin-component.js';import useQuasar from 'quasar/src/composables/use-quasar.js';;
import { computed, defineComponent, onMounted, ref, watchEffect } from "vue";
import { useRouter } from "vue-router";

import { useStore } from "@/store";
import { API_CREATE_JOURNAL, API_OPEN_JOURNAL, CreateJournal } from "@/store/types";

export default defineComponent({
    name: "CreateJournalDialog",
    emits: [
        // REQUIRED; need to specify some events that your
        // component will emit through useDialogPluginComponent()
        ...useDialogPluginComponent.emits,
    ],
    setup() {
        const { dialogRef, onDialogHide, onDialogOK, onDialogCancel } = useDialogPluginComponent();
        const $q = useQuasar();
        const $store = useStore();
        const $router = useRouter();

        const title = ref("");
        const submit = async () => {
            try {
                const id = await $store.dispatch(API_CREATE_JOURNAL, {
                    title: title.value,
                } as CreateJournal);
                await $store.dispatch(API_OPEN_JOURNAL, id);
                await $router.push(`/j/${id}`);
                onDialogOK();
            } catch (e) {
                $q.notify(e);
            }
        };

        return {
            dialogRef,
            onDialogHide,

            // stub: true only if user logged in
            createOnline: ref(false),
            title,
            loading: ref(false),
            submit,

            // other methods that we used in our vue html template;
            // these are part of our example (so not required)
            onOKClick() {
                submit();
                onDialogOK();
            },

            // we can passthrough onDialogCancel directly
            onCancelClick: onDialogCancel,
        };
    },
});
