
import useDialogPluginComponent from 'quasar/src/composables/use-dialog-plugin-component.js';;
import { defineComponent, PropType } from "vue";

import { useStore } from "@/store";
import { renderText } from "@/utils";

export default defineComponent({
    name: "PreviewAttachmentDialog",
    props: {
        attachment: {
            type: Object as PropType<PouchDB.Core.FullAttachment & { name: string }>,
            required: true,
        },
        entryId: {
            type: String,
            required: true,
        },
    },

    emits: [...useDialogPluginComponent.emits],

    setup() {
        // Providers
        const { dialogRef, onDialogHide, onDialogOK, onDialogCancel } = useDialogPluginComponent();
        const $store = useStore();

        return {
            dialogRef,
            onDialogHide,

            onOKClick() {
                onDialogOK();
            },

            onCancelClick: onDialogCancel,
            entryAttachment: $store.getters.entryAttachment,
            renderText,
        };
    },
});
