
import format from 'quasar/src/utils/format.js';import useDialogPluginComponent from 'quasar/src/composables/use-dialog-plugin-component.js';import useQuasar from 'quasar/src/composables/use-quasar.js';;
import { computed, defineComponent, PropType, ref } from "vue";

import CropAttachmentDialog from "@/components/attachments/CropAttachmentDialog.vue";
import { AttachmentNameLength } from "@/constants";
import { CreateAttachment } from "@/store/types";

export default defineComponent({
    name: "CreateAttachmentsDialog",
    props: {
        attachments: {
            type: Object as PropType<{ [key: string]: PouchDB.Core.FullAttachment }>,
            required: true,
        },
        replace: {
            type: String,
        },
        replaceData: {
            type: String,
        },
    },

    emits: [...useDialogPluginComponent.emits],

    setup(props, { emit }) {
        // Providers
        const { dialogRef, onDialogHide, onDialogOK, onDialogCancel } = useDialogPluginComponent();
        const $q = useQuasar();

        // Refs
        const currentTab = ref(props.replaceData ? "raw" : "upload");
        const uploadData = ref<File | undefined>(undefined);
        const rawData = ref(props.replaceData ? props.replaceData : "");
        const attachmentName = ref(props.replace ? props.replace : "");

        // Computed
        const rawDataSize = computed(() => format.humanStorageSize(new Blob([rawData.value]).size));
        const uploadDataSize = computed(() =>
            format.humanStorageSize(uploadData.value ? uploadData.value.size : 0)
        );

        // Functions
        const submit = () => {
            if (attachmentName.value.length >= AttachmentNameLength) {
                attachmentName.value = attachmentName.value.slice(0, AttachmentNameLength);
            }
            try {
                switch (currentTab.value) {
                    case "upload":
                        if (!uploadData.value) {
                            throw new Error("File never uploaded");
                        }
                        onDialogOK({
                            data: uploadData.value,
                            name: attachmentName.value,
                        } as CreateAttachment);
                        break;
                    case "raw":
                        onDialogOK({
                            data: rawData.value,
                            name: attachmentName.value,
                        } as CreateAttachment);
                        break;
                }
            } catch (e) {
                $q.notify({ type: "negative", message: e.message });
            }
        };
        const updateUploadData = (f: File | undefined) => {
            if (f && f.type.startsWith("image")) {
                $q.dialog({
                    component: CropAttachmentDialog,
                    componentProps: {
                        image: f,
                        imageType: f.type,
                    },
                }).onOk((b: Blob) => {
                    const f2 = new File([b], f.name, { type: f.type });
                    uploadData.value = f2;
                });
            } else {
                uploadData.value = f;
            }
        };
        const presubmit = () => {
            try {
                let cancel = false;
                if (!attachmentName.value) {
                    throw new Error("Attachment name empty");
                }
                if (!/^\w+$/.test(attachmentName.value)) {
                    throw new Error("Invalid attachment name");
                }
                // Check if attachment already exists
                if (
                    attachmentName.value in props.attachments &&
                    !(props.replace === attachmentName.value)
                ) {
                    $q.dialog({
                        title: "Attachment already exists",
                        message: `Attachment "${attachmentName.value}" already exists in this entry. Would you like to replace it?`,
                        cancel: true,
                        ok: "replace",
                        persistent: true,
                    })
                        .onOk(() => {
                            submit();
                        })
                        .onCancel(() => {
                            cancel = true;
                        });
                } else {
                    submit();
                }
            } catch (e) {
                $q.notify({ type: "negative", message: e.message });
            }
        };

        return {
            dialogRef,
            onDialogHide,

            currentTab,
            rawData,
            uploadData,
            rawDataSize,
            uploadDataSize,
            attachmentName,
            AttachmentNameLength,
            updateUploadData,

            onOKClick() {
                presubmit();
                // onDialogOK();
            },

            // we can passthrough onDialogCancel directly
            onCancelClick: onDialogCancel,
        };
    },
});
