
import { defineComponent, PropType } from "vue";

import { Entry } from "@/store/types";

import SelectAttachmentsDialog from "./attachments/SelectAttachmentsDialog.vue";

interface EntryIconDialogData {
    selectedImage?: File;
    tab: "icon" | "image";
    iconGroups: {
        category: string;
        icons: string[];
    }[];
}

export default defineComponent({
    name: "EntryIconDialog",
    props: {
        useAttachments: {
            type: Boolean,
            default: false,
        },
        entry: {
            type: Object as PropType<Entry>,
        },
    },
    emits: ["attach"],
    methods: {
        attachImage() {
            if (this.selectedImage) {
                this.$emit("attach", { type: "image", data: this.selectedImage });
            }
        },
        attachIcon(icon: string) {
            this.$emit("attach", { type: "icon", data: icon });
        },
        selectAttachment() {
            if (!this.entry) {
                return;
            }
            this.$q
                .dialog({
                    component: SelectAttachmentsDialog,
                    componentProps: {
                        entryId: this.entry.id,
                    },
                })
                .onOk((val: string) => {
                    this.$emit("attach", { type: "attachment", data: val });
                });
        },
    },
    data() {
        return {
            selectedImage: undefined,
            tab: "icon",
            iconGroups: [
                {
                    category: "Standard",
                    icons: ["subject", "person", "map"],
                },
                {
                    category: "Figures",
                    icons: [
                        "person",
                        "group",
                        "groups",
                        "directions_run",
                        "elderly",
                        "emoji_people",
                        "self_improvement",
                        "record_voice_over",
                        "manage_accounts",
                        "accessible",
                        "sentiment_very_satisfied",
                        "sentiment_satisfied",
                        "sentiment_neutral",
                        "sentiment_dissatisfied",
                        "sentiment_very_dissatisfied",
                        "mood_bad",
                    ],
                },
                {
                    category: "Locations",
                    icons: [
                        "home",
                        "apartment",
                        "corporate_fare",
                        "fence",
                        "holiday_village",
                        "foundation",
                        "festival",
                        "store_mall_directory",
                        "storefront",
                        "terrain",
                        "map",
                        "park",
                        "place",
                        "attractions",
                        "home_work",
                        "public",
                    ],
                },
                {
                    category: "Miscellaneous",
                    icons: [
                        "radio_button_checked",
                        "radio_button_unchecked",
                        "trip_origin",
                        "lens",
                        "adjust",
                        "device_hub",
                        "bubble_chart",
                        "error",
                        "stars",
                        "build_circle",
                    ],
                },
            ],
        } as EntryIconDialogData;
    },
});
